import React from "react"
import PropTypes from "prop-types"
import "./Movie.css";

function Movie ({ year, rate, title, summary, poster, genres}) {
    return (
      <div className="movie">
          <img src={poster} alt={title} title={title}/> 
        <div className="movie_data">
          <h3 className="movie_title">{title}</h3>
          <h5 className="movie_year">On {year}</h5>
          <h5 className="movie_genres">Genres : {(genres.map((genre, index) => (<span key={index}>{genre}. </span>)))}</h5>
          <h3 className="movie_rate">{rate}âœ¨</h3>
          <b>Summary</b>
          <hr></hr>
          <h5 className="movie_summary">{summary.slice(0, 180)}...{Button(summary)}
              </h5> 
              
        </div>
      </div>)
}

function Button (wtfev) {
     
        if (wtfev.length > 140) {
           return <button className="btn_showmore">show more</button>
        }
    
}

Movie.propTypes = {
    genres: PropTypes.arrayOf(PropTypes.string),
    year: PropTypes.number.isRequired,
    rate: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summmary: PropTypes.string,
    poster: PropTypes.string.isRequired,
}

export default Movie;